<template>
    <div class="index-layout" v-cloak>
        <IndexHeaderView></IndexHeaderView>
        <IndexMainView></IndexMainView>
    </div>
</template>
<script>
  import {getSignInfo} from '../../api/tiezidetail';
  import timeUtil from '../../utils/timeutil'
  import ftime from '../../utils/ftime'
  import wx from 'weixin-js-sdk';
  import Api from '../../api/api';
    export default {
        name: 'app',
        data () {
            return {
               msg:'index',
            }
        },
        components:{
            IndexHeaderView:function (resolve) {
              require(['./IndexHeader.vue'], resolve)
            },
            IndexMainView:function (resolve) {
              require(['./IndexMain.vue'], resolve)
            },
        },
      mounted(){
          if(this.isWeiXin()){
              this.getAsign();
          }
      },
      methods:{
        /**
         * 判断是否是微信浏览器打开
         * */
        isWeiXin(){
          //判断是否是微信浏览器
          let ua = window.navigator.userAgent.toLowerCase();
          if (ua.match(/MicroMessenger/i) == 'micromessenger') {
            return true;
          } else {
            return false;
          }
        },
        getAsign(){
          const _this = this;
          //将这个动态的url传递给后端(便于后端进行签名操作)
          let url = encodeURIComponent(window.location.href.split('#')[0]);
          //请求分享获取到的签名信息
          getSignInfo(Api.SIGN,url).then((res)=>{
            _this.initAsign(res);
          },(err)=>{
          });
        },
        /**
         * 初始化签名信息
         * */
        initAsign(res){
          const _this = this;
          if(res.status === 200){
            //从服务器端获取到必要的参数,将必要的信息进行初始化赋值
            let sinObj = res.data.data.detail;
            wx.config({
              debug: false,
              appId: sinObj.appId, // 和获取Ticke的必须一样------必填，公众号的唯一标识
              timestamp:sinObj.timestamp.toString(), // 必填，生成签名的时间戳
              nonceStr: sinObj.nonceStr, // 必填，生成签名的随机串
              signature: sinObj.signature,// 必填，签名，见附录1
              //需要分享的列表项:发送给朋友，分享到朋友圈，分享到QQ，分享到QQ空间
              jsApiList: [
                'onMenuShareAppMessage','onMenuShareTimeline',
                'onMenuShareQQ','onMenuShareQZone'
              ]
            });
          }
        },
      }
    }
</script>
<style scoped>
    @import "../../assets/css/index.css";
</style>



